body {
  background-color: #fdfdfd; }

.container {
  margin-left: auto;
  margin-right: auto;
  margin-top: 4rem;
  max-width: 64rem; }
  .container .content {
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 4rem;
    padding-top: 4rem;
    width: 80%; }

.my-shadow {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.25); }

a {
  text-decoration: none;
  color: #357edd; }

h1, h2, h3 {
  color: #212121; }

p {
  color: #757575;
  line-height: 1.6; }

p:last-child {
  padding-top: 1.5rem;
  opacity: 0.8; }

.my-red {
  color: #d25555; }

.black-87 {
  color: #212121; }

.h1p5 {
  height: 1.5rem; }

.w1p5 {
  height: 1.5rem; }

.homepage h3 {
  font-weight: 500; }

.homepage h4, .homepage h5, .homepage h6 {
  color: #d25555;
  margin-bottom: 0;
  padding-bottom: 0.5rem; }

.homepage p {
  margin-top: 0; }

.blogpage p {
  color: #212121;
  font-weight: 300;
  line-height: 1.8;
  padding-top: 0; }

.blogpage h1 {
  margin-bottom: 1rem; }

.blogpage h1, .blogpage h2, .blogpage h3 {
  color: #d25555; }

.blogpage code {
  border-radius: 0.25rem;
  line-height: 1.5; }

.heebo {
  font-family: 'Heebo', Arial, Helvetica, sans-serif; }

